.statistics {
  width: 400px;
  background-color: #e0ffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-top: 50px;
  box-shadow: 20px -15px 10px 5px rgba(0, 0, 0, 0.5);
}
.title {
  color: #808080;
  font-size: 30px;
}

.statList {
  min-height: 80px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80px;
  padding-top: 8px;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
}
.label {
  margin-bottom: 8px;
}
.percentage {
  font-size: 22px;
}
